<template>
  <div>
    <NavHeader/>
    <div class="section">
       <div>
         <transition name="btn">
            <router-view/>
         </transition>
       </div>
    </div>
    <NavFooter/>
  </div>
</template>

<script>
// @ is an alias to /src
import NavHeader from '@/components/NavHeader.vue'
import NavFooter from '@/components/NavFooter.vue'

export default {
  data:function(){
     return {
         mdShow:false,
     }
  },
  methods:{
    closeModal(){
         this.mdShow = false;
    },
    showFilterPop(){//点击的时候改成true，让其弹出
         this.filterBy=true;
         this.overLayFlag=true;
    },
    closePop(){//点击空白页的时候改成false，让其缩进
         this.filterBy=false;
         this.overLayFlag=false;
         this.mdShowCart = false;
    }
  },
  components: {
    NavHeader,
    NavFooter
  },
  watch:{

  },
  mounted() {
    //需要执行的js文件，需要dom操作的
    //还有bus机制挂在的地方
  },
}
</script>
<style scoped>
.section{
    width:100%;
    background:rgb(67,67,67);
}
.btn-enter-active{
  /* 进入动画进行中 */
    transition: linear all .5s;
}
/* 开始时候 */
.btn-enter{
    opacity:0;
}
/* 终止时候 */
.btn-enter-to{
    opacity:1;
}

</style>



