<template>
      <div class="main clearb">
            <div class="mainO">
                <div id="echarts" :style="bg" @dblclick="doshow" class="fl"></div>
                <div class="box">
                    <fieldset>
                        <table>
                            <th>Title:<input type="text" v-model="wenben"></th>
                            <tr>
                                <th>data:<input type="text" v-model="gate[0]"></th>
                                <th>data:<input type="text" v-model="gate[1]"></th>
                                <th>data:<input type="text" v-model="gate[2]"></th>
                                <th>data:<input type="text" v-model="gate[3]"></th>
                                <th>data:<input type="text" v-model="gate[4]"></th>
                                <th>data:<input type="text" v-model="gate[5]"></th>
                                <th>data:<input type="text" v-model="gate[6]"></th>
                                <th>data:<input type="text" v-model="gate[7]"></th>
                            </tr>
                            <tr>
                                <th>data:<input type="text" v-model="rate[0]"></th>
                                <th>data:<input type="text" v-model="rate[1]"></th>
                                <th>data:<input type="text" v-model="rate[2]"></th>
                                <th>data:<input type="text" v-model="rate[3]"></th>
                                <th>data:<input type="text" v-model="rate[4]"></th>
                                <th>data:<input type="text" v-model="rate[5]"></th>
                                <th>data:<input type="text" v-model="rate[6]"></th>
                                <th>data:<input type="text" v-model="rate[7]"></th>
                            </tr>
                        </table>
                        colorOne:<select name="" v-model="colo">
                            <option value="">Please choose</option>
                            <option value="#aaa">gray</option>
                            <option value="#345">lingthblue</option>
                            <option value="#98d">red</option>
                        </select>
                        colorTwo: <select name="" v-model="colt">
                            <option value="">Please choose</option>
                            <option value="#B5C334">gray</option>
                            <option value="#345">lingthblue</option>
                            <option value="#98d">red</option>
                        </select>
                            <legend>Parameter configuration:</legend>
                            <input type="radio" value="#061325" name="2" v-model="colf">dark
                            <input type="radio" value="#eee" name="2" v-model="colf">white
                            <input type="radio" value="#F9F4D9" name="2" v-model="colf">gary 
                            <input type="radio" value="" name="2" v-model="colf">clear 
                        </fieldset>
                </div>
            </div>
        </div>       
</template>

<script>
    // 引入基本模板      这边可以单个引入需要什么引入什么
    // let echarts = require('echarts/lib/echarts')
   // 引入柱状图组件
//    require('echarts/lib/chart/bar')
//    require('echarts/lib/chart/line')
//    require('echarts/lib/component/tooltip')
//    require('echarts/lib/component/title')
//    require('echarts/lib/component/toolbox')
//    require('echarts/lib/component/legend')
    import echarts from 'echarts'
    export default {
        data(){
            return {
                  date:['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'],
                  wenben:"try it",
                  gate:[2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3],
                  rate:[2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0, 2.3],
                  colo:"#2EC7C9",
                  colt:"#B6A2DE",
                  colf:""
            }
        },
        components: {
           
        },
        methods: {
            doit(){
               let timer = null;
               let sh = 10;
               timer = setInterval(()=>{
                     if(this.gate.length < 5){
                         this.gate.push(sh++);
                     }else{
                         clearInterval(timer);
                         timer = null;
                     }
                },100)
            },
            doshow(){
                let show = document.getElementsByClassName("box")[0];
                if(show.offsetHeight == "0"){
                    show.style.height = "300px";
                }else{
                    show.style.height = "0px";
                }
            },
            draw(){                        
                let myChart = echarts.init(document.getElementById('echarts'));
                let option = {
                        title : {
                            text: this.wenben,
                        },
                        tooltip : {
                            trigger: 'axis'
                        },
                        legend: {
                            data:['Vaporization','Precipitation']
                        },
                        color: [this.colo, this.colt],
                        toolbox: {
                            show : true,
                            feature : {
                                mark : {show: true},
                                dataView : {show: true, readOnly: false},
                                magicType : {show: true, type: ['line', 'bar']},
                                restore : {show: true},
                                saveAsImage : {show: true}
                            }
                        },
                        calculable : true,
                        xAxis : [
                            {
                                type : 'category',
                                data : this.date,
                                axisLine:{
                                    lineStyle:{
                                        color:"#008ACD",
                                        width:2
                                    }
                                }
                            }
                        ],
                        yAxis : [
                            {
                                type : 'value',
                                axisLine:{
                                    lineStyle:{
                                        color:"#008ACD",
                                        width:2
                                    }
                                }
                            }
                        ],
                        series : [
                            {
                                name:'Vaporization',
                                type:'bar',
                                data:this.gate,
                                itemStyle:{
                                    emphasis:{//鼠标一移上去出现的效果
                                        barBordeRadius:[10,10,0,0]
                                    },
                                    normal:{//初始化效果
                                        barBorderRadius:[10,10,0,0]
                                    }
                                },
                                markPoint : {
                                    data : [
                                        {type : 'max', name: 'max'},
                                        {type : 'min', name: 'min'}
                                    ]
                                },
                                markLine : {
                                    data : [
                                        {type : 'average', name: 'avg'}
                                    ]
                                }
                            },
                            {
                                name:'Precipitation',
                                type:'bar',
                                data:this.rate,
                                itemStyle:{
                                   emphasis:{
                                       barBorderRadius:[10,10,0,0]
                                   },
                                   normal:{
                                       barBorderRadius:[10,10,0,0]
                                   }
                                },
                                markPoint : {
                                    data : [
                                        {name : 'The highest in the year', value : 182.2, xAxis: 7, yAxis: 183, symbolSize:18},
                                        {name : 'Lowest in the year', value : 2.3, xAxis: 11, yAxis: 3}
                                    ]
                                },
                                markLine : {
                                    data : [
                                        {type : 'average', name : 'avg'}
                                    ]
                                }
                            }
                        ]
                    };
                myChart.setOption(option);
            }     
        },
        computed: {
            bg:function(){//计算属性控制style
                var s = {
                    "background":this.colf
                }
                return s;
            },
            bs:()=>{//计算属性控制class
                 var t = {
                     //前面写css定义好的类名，后面写控制这些是否调用为true还是false,也可以写个三目运算
                     "col":this.colf,
                     "ctf":this.colf
                 }
                 return t
            }
        },
        watch: {
            wenben:function(){
                this.draw();
            },
            gate:function(){
                this.draw();
            },
            rate:function(){
                this.draw();
            },
            colo:function(){
                this.draw();
            },
            colt:function(){
                this.draw();
            }
        },
        mounted(){
            this.draw();
        }
    }
</script>

<style scoped>
     #echarts{
         width:800px;
         height:500px;
         opacity:.5;
         border-radius:5px;
         margin-left:30px;
         margin-right:10px;
         background: #F9F4D9;
     }
     .box{
         height:0px;
         overflow:hidden;
         transition:linear all .5s;
         background: #aaa;
         opacity: .5;
         border-radius:5px;
     }
     input{
         width:50px;
         border:none;
         margin-top:5px;
         border-radius:3px;
     }
     select{
         margin-top:5px;
     }
     .mainO{
         margin-top:40px;
     }
</style>


