<template>
    <div class="HomeThird clearb">
        <form class="Data_box">
             <input v-for="(item,index) of chartsOne.week" :key="index" v-model="chartsOne.week[index]" type="text">
             <input v-for="(item,index) of chartsOne.Deal" :key="index" v-model="chartsOne.Deal[index]" type="text">
             <input v-for="(item,index) of chartsOne.Prepurchase" :key="index" v-model="chartsOne.Prepurchase[index]" type="text">
             <input v-for="(item,index) of chartsOne.intention" :key="index" v-model="chartsOne.intention[index]" type="text">
        </form>
        <div class="HomeThird_Header">
            <h2 style="color:#eee;">Product display</h2>
        </div>
        <div class="HomeThird_Body">
             <div class="Body_one fl">
                   <img src="../../assets/img/home/scatked.png" alt="" class="chart" data="line1-charts">
                   <img src="../../assets/img/home/line1.png" alt="" class="chart" data="line2-charts">
                   <img src="../../assets/img/home/pie.png" alt="" class="chart">
                   <img src="../../assets/img/home/doughnut.png" alt="" class="chart">
             </div>
             <div class="Body_one fr">
                   <img src="../../assets/img/home/pic11.png" alt="" class="picture">
                   <img src="../../assets/img/home/pic12.png" alt="" class="picture">
                   <img src="../../assets/img/home/pic13.png" alt="" class="picture">
                   <img src="../../assets/img/home/pic14.png" alt="" class="picture">
             </div>
             <div class="Body_two">
                   
             </div>
             <button v-on:click="jump">点击查看</button>
        </div>
    </div>
</template>
<script>
    import "../../assets/css/home.css"
    import echarts from 'echarts'
    export default {
        data(){
            return {
                chartsOne:{
                    week:[],
                    Deal:[],
                    Prepurchase:[],
                    intention:[]
                }
            }
        },
        components: {},
        computed: {},
        watch: {
              'chartsOne':{
                   handler:function(){
                       this.render("1","line1-charts");
                   },
                   deep:true
               }
        },
        beforeCreate(){
            // this.getData();
        },
        mounted(){
            this.getData();
            this.doit();
        },
        methods: {
              getData(){
                   this.$axios({
                       method:'get',
                       url:'/chart/getData'
                   }).then((response)=>{
                       let res = response.data;
                       for(var i = 0; i<res.length;i++){
                           var {week,Deal,Prepurchase,intention} = res[i];
                                this.chartsOne.week.push(week);
                                this.chartsOne.Deal.push(Deal);
                                this.chartsOne.Prepurchase.push(Prepurchase);
                                this.chartsOne.intention.push(intention);
                       }
                   }).catch((err)=>{
                       console.log('未查询到数据库中信息')
                   })
               },
              render(i,dz){//用来渲染的函数
                    var option = this.chart(i);
                    var myChart = echarts.init(document.getElementById(dz));
                    myChart.setOption(option,true);  
               },
              chart(i){//数据驱动模块
                if(i == 1){
                     var optipn;
                     return  option = {
                            title : {
                                text:'我的图表',
                            },
                            tooltip : {
                                trigger: 'axis'
                            },
                            legend: {
                                data:['意向','预购','成交']
                            },
                            toolbox: {
                                show : true,
                                feature : {
                                    mark : {show: true},
                                    dataView : {show: true, readOnly: false},
                                    magicType : {show: true, type: ['line', 'bar', 'stack', 'tiled']},
                                    restore : {show: true},
                                    saveAsImage : {show: true}
                                }
                            },
                            calculable : true,
                            xAxis : [
                                {
                                    type : 'category',
                                    boundaryGap : false,
                                    data : this.chartsOne.week
                                }
                            ],
                            yAxis : [
                                {
                                    type : 'value'
                                }
                            ],
                            series : [
                                {
                                    name:'成交',
                                    type:'line',
                                    smooth:true,
                                    itemStyle: {
                                        normal: {
                                            areaStyle: {type: 'default'},
                                            barBorderRadius:[5,5,5,5]
                                        },
                                        emphasis:{//鼠标一移上去出现的效果
                                            barBordeRadius:[5,5,5,5]
                                        }
                                    },
                                    data:this.chartsOne.Deal
                                },
                                {
                                    name:'预购',
                                    type:'line',
                                    smooth:true,
                                    itemStyle: {
                                        normal: {
                                            areaStyle: {type: 'default'},
                                            barBorderRadius:[5,5,5,5]
                                        },
                                        emphasis:{//鼠标一移上去出现的效果
                                            barBordeRadius:[5,5,5,5]
                                        }
                                    },
                                    data:this.chartsOne.Prepurchase
                                },
                                {
                                    name:'意向',
                                    type:'line',
                                    smooth:true,
                                    itemStyle: {
                                        normal: {
                                            areaStyle: {type: 'default'},
                                            barBorderRadius:[5,5,5,5]
                                        },
                                        emphasis:{//鼠标一移上去出现的效果
                                            barBordeRadius:[5,5,5,5]
                                        }
                                    },
                                    data:this.chartsOne.intention
                                }
                            ]
                        };
                }else if(i == 2){
                       var option;
                       return option = {
                                title : {
                                    text: '上海市闵行区',
                                    subtext: '2017年'
                                },
                                tooltip : {
                                    trigger: 'axis'
                                },
                                legend: {
                                    data:['蒸发量','降水量']
                                },
                                color: ['#2EC7C9', '#B6A2DE'],
                                toolbox: {
                                    show : true,
                                    feature : {
                                        mark : {show: true},
                                        dataView : {show: true, readOnly: false},
                                        magicType : {show: true, type: ['line', 'bar']},
                                        restore : {show: true},
                                        saveAsImage : {show: true}
                                    }
                                },
                                calculable : true,
                                xAxis : [
                                    {
                                        type : 'category',
                                        data : ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月']
                                    }
                                ],
                                yAxis : [
                                    {
                                        type : 'value'
                                    }
                                ],
                                series : [
                                    {
                                        name:'蒸发量',
                                        type:'bar',
                                        data:[2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3],
                                        itemStyle:{
                                            emphasis:{//鼠标一移上去出现的效果
                                                barBordeRadius:[10,10,0,0]
                                            },
                                            normal:{//初始化效果
                                                barBorderRadius:[10,10,0,0]
                                            }
                                        },
                                        markPoint : {
                                            data : [
                                                {type : 'max', name: '最大值'},
                                                {type : 'min', name: '最小值'}
                                            ]
                                        },
                                        markLine : {
                                            data : [
                                                {type : 'average', name: '平均值'}
                                            ]
                                        }
                                    },
                                    {
                                        name:'降水量',
                                        type:'bar',
                                        data:[2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0, 2.3],
                                        itemStyle:{
                                            emphasis:{//鼠标一移上去出现的效果
                                                barBordeRadius:[10,10,0,0]
                                            },
                                            normal:{//初始化效果
                                                barBorderRadius:[10,10,0,0]
                                            }
                                        },
                                        markPoint : {
                                            data : [
                                                {name : '年最高', value : 182.2, xAxis: 7, yAxis: 183, symbolSize:18},
                                                {name : '年最低', value : 2.3, xAxis: 11, yAxis: 3}
                                            ]
                                        },
                                        markLine : {
                                            data : [
                                                {type : 'average', name : '平均值'}
                                            ]
                                        }
                                    }
                                ]
                            };
                }
              },
              drag({needDrag,targetDrag,imgClassName,chartClassName,mainTarget}){
                 var length = needDrag.length;
                 console.log(length);
                     for(var i = 0;i < length; i++){
                        (function(m){//闭包实现
                            needDrag[m].ondragstart = function(evt){
                                //在拖拽开始时当前这个对象有dataTransfer属性,用setData方法存入一个属性为key，值为当前拖拽对象的id
                                evt.dataTransfer.setData("on",m);//这个是把当前对象下标也传过来
                            }
                        }(i));
                    }
                     targetDrag.ondragover = function(evt){//拖入指定区域时触发事件
                        evt.preventDefault();
                        console.log("doing.......")
                    }
                    targetDrag.ondrop = (evt)=>{//在vue内这里不调用箭头就访问不到this.render这个函数
                        //拖拽放弃的时候，拿到当前存储的className值，和当前的下标值
                        var on = parseInt(evt.dataTransfer.getData("on"));
                        evt.preventDefault();
                        var myElement = mainTarget.getElementsByTagName("img")[on];//查找所有对应的图片并且把下标也传过来
                            if(myElement.className == chartClassName){
                                var myNewElement = document.createElement("div");
                                var idz = myElement.getAttribute("data")+"";
                                myNewElement.setAttribute("id",idz);
                                myNewElement.setAttribute("v-on:click","jump");//出生的时候自带点击事件
                                targetDrag.appendChild(myNewElement); //在div下面插入这个新建的img
                                var m = idz[4];//截取当前元素需要给div的id值
                                this.render(m,idz);   //渲染         
                            }else if(myElement.className == imgClassName){
                                var myNewElement = document.createElement("img");//创建一个img元素
                                    myNewElement.src = myElement.src;//让指定id的src属性赋值给创建的img
                                    targetDrag.appendChild(myNewElement); //在div下面插入这个新建的img
                            }
                        }
              },
              doit(){
                var all = document.getElementsByClassName("HomeThird_Body")[0];
                var dragItem = all.getElementsByTagName("img");
                var dropLoc = all.getElementsByClassName("Body_two")[0];
                console.log("开始拖拽")
                this.drag({
                        needDrag:dragItem,
                        targetDrag:dropLoc,
                        imgClassName:"picture",
                        chartClassName:"chart",
                        mainTarget:all
                    });
              },
              jump(){
                    var target = document.getElementsByClassName('Data_box')[0];
                    var nn = target.offsetLeft;
                        if(nn == "-270"){
                            target.style.left = 0+"px";
                        }else{
                            target.style.left = -270+"px";
                        }
              }
        }
    }
</script>

<style scoped>

</style>


