<template>
<div class="main">
<p class="dll">
    <img src="../../assets/img/home/wenan.png" alt="">
    <span><i>Welcome login</i></span>
</p>
<div id="dl">
	  <table></table>
	  <div class="black">
	     <form action="">
		   <table>
		     <tr>
			   <td>
			     <h4>Login interface</h4>
			   </td>
			   <td>
			     <a href="http://localhost/dashboard/infogram/info/regesiter/register.html">User registration</a>
			   </td>	
			 </tr>
		     <tr>
			   <td colspan="2"><hr></td>
			 </tr>
		     <tr>
			   <td colspan="2">
			     <input type="text" class="my_width" placeholder="Please enter you username" v-model="uname">
			   </td>
			 </tr>
		     <tr>
			   <td colspan="2">
			     <input type="password" @keyup.enter="doLogin" class="my_width" placeholder="Please enter you password" v-model="upwd">
			   </td>
			 </tr>
		     <tr>
			   <td>
			     <input type="checkbox" class="login"><span class="autodl">Remember</span>
			   </td>
			   <td>
			     <a href="#">Forgot？</a>
			   </td>
			 </tr>
		     <tr>
			   <td colspan="2">
			     <input type="button" class="my_width" id="login"  value="Login" @click="doLogin">
			   </td>	
			 </tr>
		   </table>
		 </form>
	  </div> 
	</div>
  </div>
</template>

<script>
    import '../../assets/css/bootstrap.css'
    import "../../assets/js/jquery.js"
    import "../../assets/js/bootstrap.js"
    export default {
        data(){
            return {
                uname:"",
                upwd:""
            }
        },
        components: {},
        methods: {
            doLogin:function(){
                this.$axios({
                    method:'post',
                    url:'/user/login',
                    data:{
                        uname:this.uname,
                        upwd:this.upwd
                    }
                }).then((res)=>{
                    console.log(res)
                    var data = res.data
                    if(data.status == 0){
                        this.$store.commit("updateUserInfo",data.result)
                        sessionStorage.setItem("islogin",true)
                        this.$router.push('/Home');//跳转界面
                    }else{
                        alert("用户名或者密码有误");
                    }
                }).catch((err)=>{
                    console.log("未查询到您要的数据");
                })
            },
        },
        computed: {},
        watch: {},
        mounted(){

        }
    }
</script>

<style scoped>
.main{
    height:40em;
}
.dll{
    height:3.8em;
    line-height:3.8em;
    color:rgb(45,45,45);
    font-size:1.9em;
    margin:0;
    background:rgb(245,245,245);
    opacity: .9;
}
.dll>img{
    margin-left:30%;
}
.dll>span{
    margin-left:5%;
}
#dl{
    background:url(../../assets/img/home/bg.png) no-repeat;
    width:100%;
    height:39.5em;
    background-size:cover;
}
#dl div.black{
    width:300px;
    height:280px;
    background:rgba(0,0,0,.2);
    margin-top:10%;
    margin-left:60%;
    padding:0em 1em;
}
#dl .black table{
    width:100%;
}
#dl .black table h4{
    color:#fff;
    margin:0;
}
#dl .black table a{
    color:#fff;
    text-decoration:none;
    font-size:12px;
}
#dl .black input.my_width{
    width:100%;
    height:32px;
    border:0;
    outline:0;
    margin-top:20px;
    padding-left:15px;
    box-sizing:border-box;
    border-radius:.3em;
}
#login{
    background-image:linear-gradient(to bottom,#27b0f6,#0aa1ed);
}
input[type="button"]{
    cursor:pointer;
}
.autodl{
    color:white;
    margin-left:.4em;
}

</style>


