<template>
    <div class="nav">
        <div class="nav_header">
              <div class="fl">
                   <router-link to="/Home/HomeFirst" class="fs">
                       <span class="glyphicon glyphicon-align-justify" aria-hidden="true"></span>&nbsp;&nbsp;Library
                   </router-link>
                   <router-link to="/Home/HomeSecond" class="fs">
                       <span class="glyphicon glyphicon-eur" aria-hidden="true"></span>&nbsp;&nbsp;Analytics
                   </router-link>
                   <router-link to="/Home/HomeThird" class="fs">
                       <span class="glyphicon glyphicon-user" aria-hidden="true"></span>&nbsp;&nbsp;Demonstration
                   </router-link>
              </div>
              <div class="fr">
                   <span class="spano">Upgrade now</span>
                   <span class="learn">
                       <span class="glyphicon glyphicon-file" aria-hidden="true"></span>&nbsp;&nbsp;learn
                   </span>
                    <div class="btn-group" v-if="nickName">
                        <button type="button" class="btn btn-danger">{{nickName}}</button>
                        <button type="button" class="btn btn-danger dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <span class="caret"></span>
                            <span class="sr-only">Toggle Dropdown</span>
                        </button>
                        <ul class="dropdown-menu" style="background:rgb(100,100,100);color:#eee;">
                            <li>
                                <a href="#"><span class="glyphicon glyphicon-cog" aria-hidden="true"></span>&nbsp;&nbsp;&nbsp;account setting</a>
                            </li>
                            <li>
                                <a href="#"><span class="glyphicon glyphicon-user" aria-hidden="true"></span>&nbsp;&nbsp;&nbsp;Public Profile</a>
                            </li>
                            <li>
                                <a href="#"><span class="glyphicon glyphicon-refresh" aria-hidden="true"></span>&nbsp;&nbsp;&nbsp;Billing</a>
                            </li>
                            <li role="separator" class="divider"></li>
                            <li>
                                <a href="#" @click="logOut"><span class="glyphicon glyphicon-off" aria-hidden="true"></span>&nbsp;&nbsp;&nbsp;LogOut</a>
                            </li>
                        </ul>
                    </div>
              </div>
        </div>
    </div>
</template>

<script>
import '../assets/css/base.css'
import '../assets/css/NavHeader.css'
    import { mapState } from 'vuex'//对象结构。代替computed去拿vuex中的数据
    export default {
        data(){
            return {
                
            }
        },
        components: {
        },
        methods: {
            checkLogin(){
                this.$axios({
                  method:'get',
                  url:'/user/checkLogin'
                }).then((res)=>{
                    var data = res.data;
                    if(data.status=="0"){
                       this.$store.commit("updateUserInfo",data.result)
                    }else{
                       console.log('数据丢失')
                    }
                });
            },
            logOut(){
                this.$axios({
                  method:'post',
                  url:'/user/logout'
                }).then((res)=>{
                    let data = res.data;
                    console.log(data);
                    if(data.status=="0"){
                       this.$store.commit("updateUserInfo",'')
                    }
                    sessionStorage.setItem("islogin",false)
                    this.$router.push('/Login')
                })
            },
        },
        computed: {
            ...mapState(['nickName'])//相当于就是用计算属性从state中拿数据
        },
        watch: {},
        mounted(){
            this.checkLogin()
        }
    }
</script>
</script>

<style>

</style>



