<template>
      <div id="main">
        <div class="echarts">
            <input type="text" v-model="chartsOne.wenben">
            <input type="text" v-model="chartsOne.dataOne[0]">
            <input type="text" v-model="chartsOne.dataOne[1]">
            <input type="text" v-model="chartsOne.dataOne[2]">
            <input type="text" v-model="chartsOne.dataOne[3]">
            <input type="text" v-model="chartsOne.dataOne[4]">
            <input type="text" v-model="chartsOne.dataOne[5]">
            <input type="text" v-model="chartsOne.dataOne[6]">
            <button @click="updateData">插入数据</button>
            <img src="../../assets/one.png" alt="" class="charts" data="line1-charts">
            <img src="../../assets/two.png" alt="" class="charts" data="line2-charts">
            <img src="../../assets/pic4.png" alt="" class="picture">
            <img src="../../assets/pic12.png" alt="" class="picture">
        </div>
        <div class="ohmygod">
        </div>
     </div>     
</template>
<script>
    import axios from 'axios'
    import "../../assets/css/echarts.css"
    import echarts from 'echarts'
    export default {
        data(){
            return {
                chartsOne:{
                    wenben:"",
                    dataOne:['周一','周二','周三','周四','周五','周六','周日']
                }
            }
        },
        components: {},
        methods: {
            render(i,dz){//用来渲染的函数
                 var option = this.chart(i);
                 var myChart = echarts.init(document.getElementById(dz));
                 myChart.setOption(option,true);  
            },
            chart(i){//数据驱动模块
                if(i == 1){
                     var optipn;
                     return  option = {
                            title : {
                                text:this.chartsOne.wenben,
                            },
                            tooltip : {
                                trigger: 'axis'
                            },
                            legend: {
                                data:['意向','预购','成交']
                            },
                            toolbox: {
                                show : true,
                                feature : {
                                    mark : {show: true},
                                    dataView : {show: true, readOnly: false},
                                    magicType : {show: true, type: ['line', 'bar', 'stack', 'tiled']},
                                    restore : {show: true},
                                    saveAsImage : {show: true}
                                }
                            },
                            calculable : true,
                            xAxis : [
                                {
                                    type : 'category',
                                    boundaryGap : false,
                                    data : this.chartsOne.dataOne
                                }
                            ],
                            yAxis : [
                                {
                                    type : 'value'
                                }
                            ],
                            series : [
                                {
                                    name:'成交',
                                    type:'line',
                                    smooth:true,
                                    itemStyle: {normal: {areaStyle: {type: 'default'}}},
                                    data:[10, 12, 21, 54, 260, 830, 710]
                                },
                                {
                                    name:'预购',
                                    type:'line',
                                    smooth:true,
                                    itemStyle: {normal: {areaStyle: {type: 'default'}}},
                                    data:[30, 182, 434, 791, 390, 30, 10]
                                },
                                {
                                    name:'意向',
                                    type:'line',
                                    smooth:true,
                                    itemStyle: {normal: {areaStyle: {type: 'default'}}},
                                    data:[1320, 1132, 601, 234, 120, 90, 20]
                                }
                            ]
                        };
                }else if(i == 2){
                       var option;
                       return option = {
                                title : {
                                    text: '纯属虚构',
                                    subtext: '纯属虚构'
                                },
                                tooltip : {
                                    trigger: 'axis'
                                },
                                legend: {
                                    data:['蒸发量','降水量']
                                },
                                toolbox: {
                                    show : true,
                                    feature : {
                                        mark : {show: true},
                                        dataView : {show: true, readOnly: false},
                                        magicType : {show: true, type: ['line', 'bar']},
                                        restore : {show: true},
                                        saveAsImage : {show: true}
                                    }
                                },
                                calculable : true,
                                xAxis : [
                                    {
                                        type : 'category',
                                        data : ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月']
                                    }
                                ],
                                yAxis : [
                                    {
                                        type : 'value'
                                    }
                                ],
                                series : [
                                    {
                                        name:'蒸发量',
                                        type:'bar',
                                        data:[2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3],
                                        markPoint : {
                                            data : [
                                                {type : 'max', name: '最大值'},
                                                {type : 'min', name: '最小值'}
                                            ]
                                        },
                                        markLine : {
                                            data : [
                                                {type : 'average', name: '平均值'}
                                            ]
                                        }
                                    },
                                    {
                                        name:'降水量',
                                        type:'bar',
                                        data:[2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0, 2.3],
                                        markPoint : {
                                            data : [
                                                {name : '年最高', value : 182.2, xAxis: 7, yAxis: 183, symbolSize:18},
                                                {name : '年最低', value : 2.3, xAxis: 11, yAxis: 3}
                                            ]
                                        },
                                        markLine : {
                                            data : [
                                                {type : 'average', name : '平均值'}
                                            ]
                                        }
                                    }
                                ]
                            };
                }
            },
            drag({needDrag,targetDrag,imgClassName,chartClassName,mainTarget}){
                 var length = needDrag.length;
                     for(var i = 0;i < length; i++){
                        (function(m){//闭包实现
                            needDrag[m].ondragstart = function(evt){
                                //在拖拽开始时当前这个对象有dataTransfer属性,用setData方法存入一个属性为key，值为当前拖拽对象的id
                                evt.dataTransfer.setData("on",m);//这个是把当前对象下标也传过来
                            }
                        }(i));
                    }
                     targetDrag.ondragover = function(evt){//拖入指定区域时触发事件
                        evt.preventDefault();
                    }
                    targetDrag.ondrop = (evt)=>{//在vue内这里不调用箭头就访问不到this.render这个函数
                        //拖拽放弃的时候，拿到当前存储的className值，和当前的下标值
                        var on = parseInt(evt.dataTransfer.getData("on"));
                        evt.preventDefault();
                        var myElement = mainTarget.getElementsByTagName("img")[on];//查找所有对应的图片并且把下标也传过来
                            if(myElement.className == chartClassName){
                                var myNewElement = document.createElement("div");
                                var idz = myElement.getAttribute("data")+"";
                                myNewElement.setAttribute("id",idz);
                                targetDrag.appendChild(myNewElement); //在div下面插入这个新建的img
                                var m = idz[4];//截取当前元素需要给div的id值
                                this.render(m,idz);   //渲染         
                            }else if(myElement.className == imgClassName){
                                var myNewElement = document.createElement("img");//创建一个img元素
                                    myNewElement.src = myElement.src;//让指定id的src属性赋值给创建的img
                                    targetDrag.appendChild(myNewElement); //在div下面插入这个新建的img
                            }

                        }
            },
            doit(){
                var all = document.getElementById("main");
                var dragItem = all.getElementsByTagName("img");
                var dropLoc = document.getElementsByClassName("ohmygod")[0];
                this.drag({
                        needDrag:dragItem,
                        targetDrag:dropLoc,
                        imgClassName:"picture",
                        chartClassName:"charts",
                        mainTarget:all
                    });
            },
            getData(){
                var url = `http://127.0.0.1:3000/product/data`;
                axios.get(url).then((data)=>{
                    console.log(data);
                    this.chartsOne.wenben = data.data[0].uname;
                    console.log(data.data[0].uname)
                }).catch((err)=>{
                    console.log("请求不到服务器程序")
                })
            },
            updateData(){
                var url = `http://127.0.0.1:3000/product/updatedata?uname=${this.chartsOne.wenben}`;
                axios.get(url).then((data)=>{
                    if(data.code == "0"){
                        alert("插入数据失败")
                    }else if(data.code == "1"){
                        alert("插入信息成功")
                    }
                }).catch((err)=>{
                    console.log("插入失败")
                })
            }
        },
        computed: {

        },
        watch: {
               'chartsOne':{
                   handler:function(){
                       this.render("1","line1-charts");
                   },
                   deep:true
               }
            // 'chartsOne.wenben':function(){//'chartsOne.wenben'能够监视对象下的单个属性值的变化
            //      this.render("1","line1-charts");//用@click的方法，在div出生时就给它加上这个属性
            // },
            // 'chartsOne.dataOne':function(){
            //     this.render("1","line1-charts");//用@click的方法，在div出生时就给它加上这个属性
            // }
        },
        beforeCreate(){
            var url = `http://127.0.0.1:3000/product/data`;
                axios.get(url).then((data)=>{
                    console.log(data);
                    this.chartsOne.wenben = data.data[0].uname;
                    console.log(data.data[0].uname)
                }).catch((err)=>{
                    console.log("请求不到服务器程序")
                })
        },
        mounted(){
            this.doit();
            // this.getData();
        }
    }
</script>

<style scoped>
    /*动态生成的图片和图表不能放在独立的style内部，要放在全局*/
       
</style>


