import Vue from 'vue'
import Router from 'vue-router'
//上面两个是全局引入

import Home from '../views/Home/Home.vue'
import Login from '../views/User/Login.vue'
import registration from '../views/User/registration.vue'
import HomeFirst from '../views/Home/HomeFirst.vue'
import HomeSecond from '../views/Home/HomeSecond.vue'
import HomeThird from '../views/Home/HomeThird.vue'
import HomeFour from '../views/Home/HomeFour.vue'
import SecondChildreno from '../views/Home/SecondChildreno.vue'
import SecondChildrent from '../views/Home/SecondChildrent.vue'
import NotFound from '../views/NotFound/NotFound.vue'
import Echarts from '../views/Echarts/echarts.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {path:"/",component:Login},
    {path:"/Login",name:'Login',component:Login,alias:'/other'},
    {path:"/registration",name:'registration',component:registration},
    {path:"/Echarts",name:'Echarts',component:Echarts},
    {path:"/Home",component:Home,children:[
        // 子组件在main中需要有router-view缺口
        {path:"/",component:HomeFirst},//只要是默认路由显示就什么都不用加，直接一个/
        {path:"/Home/HomeFirst",name:'HomeFirst',component:HomeFirst},
        {path:"/Home/HomeSecond",component:HomeSecond,children:[
           {path:"/",component:SecondChildreno},
           {path:"/HomeSecond/SecondChildreno",name:'SecondChildreno',component:SecondChildreno},
           {path:"/HomeSecond/SecondChildrent",name:'SecondChildrent',component:SecondChildrent},
        ]},
        {path:"/Home/HomeThird",name:'HomeThird',component:HomeThird,redirect:{name:'HomeFour'}},
        {path:"/Home/HomeFour",name:'HomeFour',component:HomeFour,},
    ]},
    {path:"/*",component:NotFound}
  ]
})
//进行路由的守卫
router.beforeEach((to, from, next) => {
  // console.log('navigation-guards');
  // to: Route: 即将要进入的目标 路由对象
  // from: Route: 当前导航正要离开的路由
  // next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖 next 方法的调用参数。
  const nextRoute = ['/Home','/registration','/HomeFirst','/HomeSecond'];
  let log = sessionStorage.getItem("islogin");
  let isLogin = log; // 是否登录
  // 未登录状态；当路由到nextRoute指定页时，跳转至login
  if (nextRoute.indexOf(to.path) >= 0) {
  //to.path就是命名路由守卫上面定义的时候需要定义path的路由地址
   if (!isLogin) {
    console.log('oh no');
    router.push({ name: 'Login' })
   }
  }
  // 已登录状态；当路由到login时，跳转至home 
  if (to.name === 'Login') {
   if (isLogin) {
    router.push({ path: '/Home'});
   }
  }
  next();
 });
export default router;


