// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router/index'
import store from './store'
import axios from 'axios'
import VueLazyload from 'vue-lazyload'
import infiniteScroll from  'vue-infinite-scroll'//滚动才加载插件
import {currency} from './util/currency'//计算插件

import './assets/css/base.css'

Vue.filter("currency",currency);
Vue.config.productionTip = false
Vue.prototype.$bus = new Vue();//当前的Vue项目中 都可以通过this.$bus来进行访问了
Vue.prototype.$axios = axios;//引入全局
Vue.use(VueLazyload, {//懒加载,该对应的svg文件
  loading: 'static/loading-svg/loading-bars.svg',
  try: 3 // default 1
})
Vue.use(infiniteScroll);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



